@-webkit-keyframes zp_appear {
  0% {
    -webkit-transform: scale(0.5);
    opacity: 0.1;
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 1;
  }
}
@-moz-keyframes zp_appear {
  0% {
    -moz-transform: scale(0.5);
    opacity: 0.1;
  }
  100% {
    -moz-transform: scale(1);
    opacity: 1;
  }
}
@-o-keyframes zp_appear {
  0% {
    -o-transform: scale(0.5);
    opacity: 0.1;
  }
  100% {
    -o-transform: scale(1);
    opacity: 1;
  }
}
@keyframes zp_appear {
  0% {
    transform: scale(0.5);
    opacity: 0.1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes zp_fadeOut {
  0% {
    -webkit-transform: translateY(0);
    opacity: 0.1;
  }
  100% {
    -webkit-transform: translateY(100%);
    opacity: 1;
  }
}
@-moz-keyframes zp_fadeOut {
  0% {
    -webkit-transform: translateY(0);
    opacity: 0.1;
  }
  100% {
    -webkit-transform: translateY(100%);
    opacity: 1;
  }
}
@-o-keyframes zp_fadeOut {
  0% {
    -webkit-transform: translateY(0);
    opacity: 0.1;
  }
  100% {
    -webkit-transform: translateY(100%);
    opacity: 1;
  }
}
@keyframes zp_fadeOut {
  0% {
    -webkit-transform: translateY(0);
    opacity: 0.1;
  }
  100% {
    -webkit-transform: translateY(100%);
    opacity: 1;
  }
}
@-webkit-keyframes zp_fadeUp {
  0% {
    -webkit-transform: translateY(100%);
    opacity: 0.1;
  }
  100% {
    -webkit-transform: translateY(0);
    opacity: 1;
  }
}
@-moz-keyframes zp_fadeUp {
  0% {
    -moz-transform: translateY(100%);
    opacity: 0.1;
  }
  100% {
    -moz-transform: translateY(0);
    opacity: 1;
  }
}
@-o-keyframes zp_fadeUp {
  0% {
    -o-transform: translateY(100%);
    opacity: 0.1;
  }
  100% {
    -o-transform: translateY(0);
    opacity: 1;
  }
}
@keyframes zp_fadeUp {
  0% {
    transform: translateY(100%);
    opacity: 0.1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
/* Individual modal styles with animations/transitions */
/* Added animation */
.team,
.special-services-box, .columns {
  opacity: 1;
}

.js-columns-ready .team, .js-columns-ready .special-services-box, .js-columns-ready .columns {
  opacity: 0;
}

.zp_start_animation.team,
.zp_start_animation.special-services-box,
.zp_start_animation.columns {
  -webkit-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -moz-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  opacity: 1;
  -moz-transform: scale(1);
  -webkit-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}

.js-columns-ready .portfolio_shortcode,
.js-columns-ready .zp-grid-wrapper {
  opacity: 0;
}

.zp_start_animation.portfolio_shortcode,
.zp_start_animation.zp-grid-wrapper {
  opacity: 1;
}

.zp_show {
  -webkit-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -moz-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  opacity: 1;
  display: block;
}

.zp_hide {
  -webkit-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -moz-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  animation: zp_appear 1s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275);
  opacity: 1;
  display: none;
}

