.galereya-slider.cat-port.opened {
  opacity: 100;
}

.galereya-slider.opened {
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.galereya-slider.cat-port .galereya-slider-slide {
  opacity: 100;
  background-color: #000;
  transition: opacity .5s ease-out;
  -o-transition: none;
}

.galereya-slider-slide {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: #1a1a1a url(https://489323192bc07f331d47-c06041d322f1ad6b39306f4da82d1689.ssl.cf1.rackcdn.com/wp-content/themes/argo/bower_components/galereya/dist/img/black_lozenge.png);
  background-size: 38px 38px;
  box-shadow: 0 0 20px 4px #000;
  transition: -webkit-transform .5s ease-out;
  transition: transform .5s ease-out;
  -o-transition: none;
}

.galereya-slide-img {
  max-width: 100%;
  max-height: 100%;
}

.galereya-slider.cat-port .galereya-slider-slide.next {
  opacity: 0;
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.galereya-slider-slide.next {
  -webkit-transform: translateX(110%);
  transform: translateX(110%);
}

@media only screen and (min-width: 601px) {
  .galereya-slider-slide {
    transition: -webkit-transform 1s ease-out;
    transition: transform 1s ease-out;
    -o-transition: none;
  }
}


